import React from 'react';

const ConfirmDialog = ({ message, onConfirm, onCancel, isSuccess  }) => {
  return (
    <div className="fixed inset-0 bg-[#911E82] bg-opacity-75 flex items-center justify-center z-50">
      <div className="bg-white p-6 rounded-lg shadow-lg w-1/3">
      <h3 className={`text-xl font-semibold mb-4 text-center ${isSuccess ? 'text-green-500' : 'text-red-500'}`}>
          {message}
        </h3>
        {/* <h3 className="text-xl font-semibold mb-4">{message}</h3> */}
        <div className="flex justify-center space-x-4">
        <button className="Btn" onClick={onConfirm}>
            <div className="sign">
                <svg
                viewBox="0 0 16 16"
                className="bi bi-trash3-fill"
                fill="currentColor"
                height="18"
                width="18"
                xmlns="http://www.w3.org/2000/svg"
                >
                <path
                    d="M11 1.5v1h3.5a.5.5 0 0 1 0 1h-.538l-.853 10.66A2 2 0 0 1 11.115 16h-6.23a2 2 0 0 1-1.994-1.84L2.038 3.5H1.5a.5.5 0 0 1 0-1H5v-1A1.5 1.5 0 0 1 6.5 0h3A1.5 1.5 0 0 1 11 1.5m-5 0v1h4v-1a.5.5 0 0 0-.5-.5h-3a.5.5 0 0 0-.5.5M4.5 5.029l.5 8.5a.5.5 0 1 0 .998-.06l-.5-8.5a.5.5 0 1 0-.998.06Zm6.53-.528a.5.5 0 0 0-.528.47l-.5 8.5a.5.5 0 0 0 .998.058l.5-8.5a.5.5 0 0 0-.47-.528ZM8 4.5a.5.5 0 0 0-.5.5v8.5a.5.5 0 0 0 1 0V5a.5.5 0 0 0-.5-.5"
                ></path>
                </svg>
            </div>

            <div className="text">Delete</div>
            </button>
          {/* <button
            className="bg-red-500 text-white px-4 py-2 rounded"
            onClick={onConfirm}
          >
            Confirm
          </button> */}

            <button className='cancelButton' onClick={onCancel}> Cancel
            </button>
          {/* <button
            className="bg-gray-300 text-black px-4 py-2 rounded"
            onClick={onCancel}
          >
            Cancel
          </button> */}
        </div>
      </div>
    </div>
  );
};

export default ConfirmDialog;